import { Component, EventEmitter, Input, OnInit, Output, OnDestroy } from '@angular/core';
import { Router } from '@angular/router';
import { IAcademicProgram } from 'src/app/@store/academic-programs/academic-programs.model';
import { Subscription } from 'rxjs';
import { Store } from '@ngxs/store';
import { AcademicProgramsState } from '../../../../../@store/academic-programs/academic-programs.state';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'app-programs',
  templateUrl: './programs.page.html',
  styleUrls: ['./programs.page.scss'],
})
export class ProgramsPage implements OnInit, OnDestroy {
  @Input() editable = false;
  @Input() profileId: string;
  @Output() deleteProgramClick = new EventEmitter<string>();
  @Output() itemClick = new EventEmitter<string>();
  @Output() addProgramClick = new EventEmitter<void>();
  public items: IAcademicProgram[] = [];
  private programs$: Subscription;

  constructor(private router: Router, private translateService: TranslateService, private store: Store) {}

  ngOnInit() {
    this.programs$ = this.store.select(AcademicProgramsState.getOrganizationAcademicPrograms).subscribe((data) => {
      this.items = data;
    });
  }

  public goAddProgram() {
    this.addProgramClick.emit();
  }

  public onDeleteClick(id: string) {
    this.deleteProgramClick.emit(id);
  }

  public onItemClick(id: string) {
    this.itemClick.emit(id);
  }

  private disconnectObservers(): void {
    if (this.programs$) {
      this.programs$.unsubscribe();
    }
  }

  ngOnDestroy() {
    this.disconnectObservers();
  }
}
