import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { ProfilesState } from '../../../../../@store/profiles/profiles.state';
import { Store } from '@ngxs/store';
import { IOrganizationInfo } from '../../../../../@shared/interfaces/organization-info';
import { Subscription } from 'rxjs';
import { Utils } from '../../../../../@global/utils';
import { Router } from '@angular/router';
import { ORGANIZATION_TYPE } from '../../../../../@shared/enums/organization-type';
import { UtilsService } from 'src/app/@core/utils.service';

@Component({
  selector: 'app-about',
  templateUrl: './about.page.html',
  styleUrls: ['./about.page.scss'],
})
export class AboutPage implements OnInit, OnDestroy {
  @Input() editable: boolean;

  private organizationInfo$: Subscription;
  public organizationData: IOrganizationInfo;
  public organizationLocation: string;
  public hasLocation: boolean;
  public isEmpty: boolean;

  constructor(private store: Store, private router: Router, private utilsService: UtilsService) {}

  ngOnInit() {
    this.subscribeToOrganizationInfo();
  }

  public isAboutInfoEmpty() {
    return (
      !this.organizationData.description &&
      !this.organizationData.students_size &&
      !this.organizationData.employees_size &&
      !this.organizationData.phone &&
      !this.organizationData.website &&
      !this.hasLocation
    );
  }

  private subscribeToOrganizationInfo() {
    this.organizationInfo$ = this.store
      .select(ProfilesState.getOrganizationInfo)
      .subscribe((data: IOrganizationInfo) => {
        this.organizationData = data;

        this.isEmpty = this.isAboutInfoEmpty();

        this.hasLocation = !!this.organizationData.country;

        if (this.hasLocation) {
          this.organizationLocation = Utils.generateUserFriendlyLocation(
            this.organizationData.country,
            this.organizationData.region,
            this.organizationData.city,
            this.organizationData.province,
            this.organizationData.region_name
          );
        }
      });
  }

  public goToEditPage(): void {
    const page =
      this.organizationData.organization_type === ORGANIZATION_TYPE.ACADEMICS
        ? '/edit-academic-organization-info'
        : '/edit-sport-organization-info';
    this.router.navigate([page, this.organizationData.id]);
  }

  public openLink(link): void {
    this.utilsService.openLink(link);
  }

  private disconnectObservers(): void {
    if (this.organizationInfo$) {
      this.organizationInfo$.unsubscribe();
    }
  }

  ngOnDestroy() {
    this.disconnectObservers();
  }
}
