import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { Router } from '@angular/router';
import { Subscription } from 'rxjs';
import { Store } from '@ngxs/store';
import { JobOffersState } from '../../../../../@store/job-offers/job-offers.state';
import { IJobOffer } from '../../../../../@store/job-offers/job-offers.model';

@Component({
  selector: 'app-jobs',
  templateUrl: './jobs.page.html',
  styleUrls: ['./jobs.page.scss'],
})
export class JobsPage implements OnInit {
  @Input() editable: boolean;
  @Input() profileId: string;
  @Output() itemClick = new EventEmitter<string>();
  @Output() deleteOpportunityClick = new EventEmitter<string>();
  @Output() addOpportunityClick = new EventEmitter<void>();

  private opportunities$: Subscription;
  public items: IJobOffer[];

  constructor(private router: Router, private store: Store) {}

  ngOnInit() {
    this.opportunities$ = this.store.select(JobOffersState.getJobOffers).subscribe((data) => {
      this.items = data;
    });
  }

  addItem() {
    this.router.navigate(['organization-profile', this.profileId, 'opportunity']);
  }

  public onItemClick(id: string) {
    this.itemClick.emit(id);
  }

  public onDeleteClick(id: string) {
    this.deleteOpportunityClick.emit(id);
  }
}
