import { Component, OnDestroy, OnInit } from '@angular/core';
import { Select, Store } from '@ngxs/store';
import { ActivatedRoute, Router } from '@angular/router';
import { Location } from '@angular/common';
import { AlertController, PopoverController } from '@ionic/angular';
import { BasePage } from '../../../base/base.page';
import { Constants } from '../../../@global/constants';
import { Utils } from '../../../@global/utils';
import { PopoverHeaderComponent } from './components/popover-header/popover-header.component';
import { Observable, Subscription } from 'rxjs';
import { IOrganizationInfo } from '../../../@shared/interfaces/organization-info';
import { UtilsService } from '../../../@core/utils.service';
import {
  GetOrganizationProfileInfo,
  SetShowProfileNewPostButton,
  SetCurrentProfileId,
  SetProfilesFirstPublicationReference,
} from '../../../@store/profiles/profiles.actions';
import { PagesState } from '../../../@store/pages/pages.state';
import { ProfilesState } from '../../../@store/profiles/profiles.state';
import { TranslateService } from '@ngx-translate/core';
import {
  DeletePage,
  FollowPageAction,
  GetFollowedPages,
  UnfollowPageAction,
} from '../../../@store/pages/pages.actions';
import {
  DeleteJobOfferAction,
  GetOrganizationJobOffersAction,
  SetCurrentJobOffer,
} from '../../../@store/job-offers/job-offers.actions';
import {
  DeleteAcademicProgram,
  GetOrganizationAcademicPrograms,
  SetCurrentAcademicProgram,
} from 'src/app/@store/academic-programs/academic-programs.actions';
import { AcademicProgramsState } from 'src/app/@store/academic-programs/academic-programs.state';
import { IAcademicProgram } from 'src/app/@store/academic-programs/academic-programs.model';
import { IPublicationList } from 'src/app/@shared/interfaces/publication-list';
import { JobOffersState } from '../../../@store/job-offers/job-offers.state';
import { IJobOffer } from '../../../@store/job-offers/job-offers.model';
import { ORGANIZATION_TYPE } from '../../../@shared/enums/organization-type';
import { finalize, take } from 'rxjs/operators';
import { IActionIcon } from '../../../@shared/components/header/header.component';
import { NavigationExtrasService } from '../../../@core/navigation-extras.service';
import { ORGANIZATION_PROFILE_TAB } from '../../../@shared/enums/organization-profile-tab';

interface TabConfig {
  title: string;
  icon: string;
  tab: ORGANIZATION_PROFILE_TAB;
}

const HEADER_SETTINGS_ICON = 'settings-outline';
const HEADER_OPTIONS_ICON = 'ellipsis-vertical';

@Component({
  selector: 'app-organization-profile',
  templateUrl: './organization-profile.page.html',
  styleUrls: ['./organization-profile.page.scss'],
})
export class OrganizationProfilePage extends BasePage implements OnInit, OnDestroy {
  public profileId: string;
  public imagePathOnboarding = Constants.ONBOARDING_IMAGE_PATH;
  public profilePublicInfo: IOrganizationInfo;
  private profilePublicInfo$: Subscription;
  public hasOrganizationLocation: boolean;
  public organizationLocation: string;
  public tabsConfig: TabConfig[] = [];
  public editable: boolean;
  public following: boolean;
  public academic: boolean;
  public profileImage: string;
  public currentTab = ORGANIZATION_PROFILE_TAB.HIGHLIGHTS;
  public organizationProbileTabs = ORGANIZATION_PROFILE_TAB;
  public headerIcon: IActionIcon;
  public prefix: string;
  private programsSubscription$: Subscription;
  public programs: IAcademicProgram[] = [];
  public itemsMenu: any[] = [];
  @Select(AcademicProgramsState.getOrganizationAcademicPrograms) programsList$: Observable<IAcademicProgram[]>;
  private highlightSubscription$: Subscription;
  public publisherHighlights: IPublicationList;
  public showSpinner: boolean;
  public inProcess = false;

  constructor(
    public router: Router,
    location: Location,
    store: Store,
    utilsService: UtilsService,
    private route: ActivatedRoute,
    private popoverController: PopoverController,
    private translateService: TranslateService,
    private alertController: AlertController,
    private navigationExtrasService: NavigationExtrasService
  ) {
    super(location, router, store, utilsService);
  }

  ngOnInit() {
    this.subscribeToProfilePublicInfo();
    this.subscribeToHighlightsChanges();
    this.subscribeToOrganizationInfo();
    this.subscribeToPrograms();
    this.goToInitialTab();
  }

  public doIonViewWillEnter() {
    this.profileId = this.route.snapshot.params['profileId'];
    this.store.dispatch(new GetOrganizationProfileInfo({ id: this.profileId }));
    this.store.dispatch(new GetFollowedPages());
    this.store.dispatch(new GetOrganizationAcademicPrograms(this.profileId));
    this.store.dispatch(new GetOrganizationJobOffersAction(this.profileId));
    this.editable = !!this.store.selectSnapshot(PagesState.getUserPageList).find((page) => page.id === this.profileId);
    this.loadPublications();
    this.setShowProfileNewPostButton();
    this.store.dispatch(new SetCurrentProfileId(this.profileId));
    this.store.dispatch(new SetProfilesFirstPublicationReference(null));
  }

  private goToInitialTab() {
    this.currentTab = this.navigationExtrasService.getExtras()?.tab || ORGANIZATION_PROFILE_TAB.HIGHLIGHTS;
    this.navigationExtrasService.setExtras(null);
  }

  private loadPublications() {
    this.showSpinner = true;
    this.utilsService
      .loadPublications(this.profileId)
      .pipe(
        finalize(() => {
          this.showSpinner = false;
        })
      )
      .subscribe();
  }

  private subscribeToPrograms() {
    this.programsSubscription$ = this.programsList$.subscribe((res) => {
      this.programs = res;
    });
  }

  private subscribeToOrganizationInfo(): void {
    this.profilePublicInfo$ = this.store.select(PagesState.getFollowedPageList).subscribe((data) => {
      if (!data) {
        return;
      }

      this.following = !this.editable && !!data.find((page) => page.id === this.profileId);
      this.headerIcon = {
        name: this.editable ? 'settings-outline' : this.following ? 'ellipsis-vertical' : '',
      };
    });
  }

  private subscribeToHighlightsChanges(): void {
    this.highlightSubscription$ = this.store.select(ProfilesState.getPublisherHighlights).subscribe(async (data) => {
      this.publisherHighlights = data;
    });
  }

  private subscribeToProfilePublicInfo(): void {
    this.profilePublicInfo$ = this.store.select(ProfilesState.getOrganizationInfo).subscribe((data) => {
      if (!data) {
        return;
      }

      this.profilePublicInfo = data;
      this.prefix = Utils.getSubtypeTranslationPrefix(this.profilePublicInfo.organization_type);

      this.profileImage = this.profilePublicInfo.photo;
      this.hasOrganizationLocation = !!this.profilePublicInfo.country;

      if (this.hasOrganizationLocation) {
        this.organizationLocation = Utils.generateUserFriendlyLocation(
          this.profilePublicInfo.country,
          this.profilePublicInfo.region,
          this.profilePublicInfo.city,
          this.profilePublicInfo.province,
          this.profilePublicInfo.region_name
        );
      }

      this.academic = this.profilePublicInfo.organization_type === ORGANIZATION_TYPE.ACADEMICS;

      this.setTabsConfig();
    });
  }

  public goToTab(path: string): void {
    // use replaceUrl so the user navigates to home when go back, instead of navigate between the diferent tabs.
    this.router.navigate([path], { replaceUrl: true });
  }

  public setTabsConfig(): void {
    this.tabsConfig = [
      {
        title: 'organizationProfile.highlights',
        icon: 'icon-highlights',
        tab: ORGANIZATION_PROFILE_TAB.HIGHLIGHTS,
      },
      {
        title: 'organizationProfile.about',
        icon: 'icon-about',
        tab: ORGANIZATION_PROFILE_TAB.ABOUT,
      },
      {
        title: 'organizationProfile.jobs',
        icon: 'icon-jobs',
        tab: ORGANIZATION_PROFILE_TAB.JOBS,
      },
    ];

    if (this.academic) {
      this.tabsConfig.splice(this.tabsConfig.length - 1, 0, {
        title: 'organizationProfile.programs',
        icon: 'icon-program',
        tab: ORGANIZATION_PROFILE_TAB.PROGRAMS,
      });
    }
  }

  public changeTab(tab: ORGANIZATION_PROFILE_TAB): void {
    this.currentTab = tab;
    this.setShowProfileNewPostButton();
  }

  private setShowProfileNewPostButton(): void {
    if (this.currentTab === ORGANIZATION_PROFILE_TAB.HIGHLIGHTS) {
      this.store.dispatch(new SetShowProfileNewPostButton(this.editable));
    } else {
      this.store.dispatch(new SetShowProfileNewPostButton(false));
    }
  }

  public async showAdminPopover(params) {
    this.itemsMenu = [
      {
        text: 'organizationProfile.edit',
        action: () => {
          this.goEditPage();
        },
      },
      {
        text: 'organizationProfile.createOpportunity',
        action: () => {
          this.goCreateOpportunity();
        },
      },
      {
        text: 'organizationProfile.manageAdmin',
        action: () => {
          this.goAdminsPage();
        },
      },
      {
        text: 'organizationProfile.delete',
        color: 'danger',
        action: () => {
          this.deletePage();
        },
      },
    ];
    if (this.academic) {
      this.itemsMenu.splice(2, 0, {
        text: 'organizationProfile.createProgram',
        action: () => {
          this.goAddProgram();
        },
      });
    }

    const popover = await this.popoverController.create({
      event: params.event,
      component: PopoverHeaderComponent,
      componentProps: { items: this.itemsMenu },
      cssClass: 'menu-popover',
      translucent: true,
    });

    popover.onDidDismiss().then((result) => {
      if (result.data !== undefined) {
        this.adminPopoverAction(result.data);
      }
    });

    await popover.present();
  }

  public async showPopover(params) {
    const items = [
      {
        text: 'unfollow',
        color: 'danger',
      },
    ];
    const popover = await this.popoverController.create({
      event: params.event,
      component: PopoverHeaderComponent,
      componentProps: { items },
      cssClass: 'menu-popover',
      translucent: true,
    });

    popover.onDidDismiss().then((result) => {
      if (result.data === 0) {
        this.showUnfollowPageConfirm();
      }
    });

    await popover.present();
  }

  private async showUnfollowPageConfirm() {
    const message = await this.translateService
      .get('organizationProfile.deletePageFollowing', { name: this.profilePublicInfo?.complete_name })
      .toPromise();

    const alert = await this.alertController.create({
      cssClass: 'custom-alert',
      message,
      buttons: [
        {
          text: this.translateService.instant(`buttons.cancel`),
          role: 'cancel',
          cssClass: 'secondary',
          handler: () => {
            // do nothing
          },
        },
        {
          text: this.translateService.instant(`buttons.accept`),
          handler: () => {
            this.unfollowPage();
          },
        },
      ],
    });

    await alert.present();
  }

  public followPage() {
    this.store.dispatch(new FollowPageAction({ organization: this.profilePublicInfo }));
  }

  public unfollowPage() {
    this.store.dispatch(new UnfollowPageAction({ id: this.profileId }));
  }

  public goEditPage() {
    const page =
      this.profilePublicInfo.organization_type === ORGANIZATION_TYPE.ACADEMICS
        ? '/edit-academic-organization-info'
        : '/edit-sport-organization-info';
    this.router.navigate([page, this.profileId]);
  }

  public goAdminsPage() {
    this.router.navigate(['/manage-admins-page', this.profileId]);
  }

  private async deletePage() {
    const message = await this.translateService.get('organizationProfile.administratorsPage.deletePage').toPromise();
    const alert = await this.alertController.create({
      cssClass: 'custom-alert',
      message,
      buttons: [
        {
          text: this.translateService.instant(`buttons.cancel`),
          role: 'cancel',
          cssClass: 'secondary',
          handler: () => {
            // do nothing
          },
        },
        {
          text: this.translateService.instant(`buttons.accept`),
          handler: () => {
            this.store
              .dispatch(new DeletePage({ id: this.profileId }))
              .pipe(take(1))
              .subscribe(() => {
                this.goBack();
              });
          },
        },
      ],
    });
    await alert.present();
  }

  public onHeaderIconClick($event) {
    if (this.editable) {
      // this.goEditPage();
      this.showAdminPopover($event);
    }
    if (this.following) {
      this.showPopover($event);
    }
  }

  public getIconHeader() {
    if (this.editable) {
      return HEADER_SETTINGS_ICON;
    }
    if (this.following) {
      return HEADER_OPTIONS_ICON;
    }
    return '';
  }

  public async deleteProgram(event) {
    const message = await this.translateService.get('organizationProfile.deleteAcademicProgram').toPromise();
    const alert = await this.alertController.create({
      cssClass: 'custom-alert',
      message,
      buttons: [
        {
          text: this.translateService.instant(`buttons.cancel`),
          role: 'cancel',
          cssClass: 'secondary',
          handler: () => {
            // do nothing
          },
        },
        {
          text: this.translateService.instant(`buttons.accept`),
          handler: () => {
            this.store.dispatch(new DeleteAcademicProgram(event));
          },
        },
      ],
    });
    await alert.present();
  }

  public onProgramClick(event) {
    const program = this.store
      .selectSnapshot(AcademicProgramsState.getOrganizationAcademicPrograms)
      .find((item) => item.id === event);
    const programUpdated: IAcademicProgram = { ...program, own: this.editable } as IAcademicProgram;
    this.store.dispatch(new SetCurrentAcademicProgram(programUpdated));
    this.router.navigate(['academic-program-detail', event]);
  }

  private adminPopoverAction(action: number) {
    this.itemsMenu[action].action();
  }

  public goAddProgram(): void {
    this.router.navigate(['organization-profile', this.profileId, 'academic-program']);
  }

  public goCreateOpportunity(): void {
    this.router.navigate(['organization-profile', this.profileId, 'opportunity']);
  }

  public onOpportunityClick(event) {
    const opportunity = this.store.selectSnapshot(JobOffersState.getJobOffers).find((item) => item.id === event);
    const opportunityUpdated: IJobOffer = { ...opportunity, own: this.editable } as IJobOffer;
    this.store.dispatch(new SetCurrentJobOffer(opportunityUpdated));
    this.router.navigate(['opportunity-detail', event]);
  }

  public async onDeleteOpportunity(id: string) {
    const selected = this.store.selectSnapshot(JobOffersState.getJobOffers).find((item) => item.id === id);
    const message = await this.translateService.get('userProfile.deleteMessage', { name: selected.title }).toPromise();

    const alert = await this.alertController.create({
      cssClass: 'custom-alert',
      message,
      buttons: [
        {
          text: this.translateService.instant(`buttons.cancel`),
          role: 'cancel',
          cssClass: 'secondary',
          handler: () => {
            // do nothing
          },
        },
        {
          text: this.translateService.instant(`buttons.accept`),
          handler: () => {
            this.deleteOpportunity(id);
          },
        },
      ],
    });

    await alert.present();
  }

  public deleteOpportunity(id: string) {
    this.store.dispatch(new DeleteJobOfferAction(id));
  }

  public goWeb(link) {
    this.utilsService.openLink(link);
  }

  private disconnectObservers(): void {
    try {
      this.profilePublicInfo$.unsubscribe();
      this.programsSubscription$.unsubscribe();
      this.highlightSubscription$.unsubscribe();
    } catch (error) {
      console.error(error);
    }
  }

  public goPageFollowers(connectionsNumber: number): void {
    if (connectionsNumber > 0) {
      this.router.navigate(['connections'], {
        queryParams: {
          id: this.profileId,
          completeName: this.profilePublicInfo.complete_name,
          page: true,
        },
      });
    }
  }

  public logScrollEnd() {
    this.loadNextPublications();
  }

  private loadNextPublications(): void {
    if (this.inProcess) {
      return;
    }
    this.inProcess = true;
    this.showSpinner = true;
    this.utilsService
      .loadNextPublications(this.publisherHighlights, this.profileId)
      .pipe(
        finalize(() => {
          this.showSpinner = false;
          this.inProcess = false;
        })
      )
      .subscribe();
  }

  public ionViewWillLeave(): void {
    this.store.dispatch(new SetShowProfileNewPostButton(false));
  }

  ngOnDestroy() {
    this.disconnectObservers();
  }
}
